<template>
  <div class="out_wrap">
    <div class="flex_between search mb22">
      <div class="left">
        <el-input v-model="queryControl.keyword" type="text" placeholder="请输入员工姓名" class="w150" clearable @keyup.native.enter="query(1)" />
        <el-input v-model="queryControl.account" type="text" placeholder="请输入员工手机号" class="ml20" style="width: 160px;" clearable @keyup.native.enter="query(1)" />
        <el-button type="primary" class="ml20" @click="query(1)">查询</el-button>
      </div>
      <div class="right">
        <!-- <el-button type="primary" @click="roleManage"><i class="el-icon-user-solid" />角色管理</el-button> -->
        <el-button type="primary" @click="create"><i class="el-icon-plus" />新增员工</el-button>
      </div>
    </div>
    <el-table :data="tableData" class="w100" tooltip-effect="dark">
      <el-table-column prop="nickname" label="员工姓名" show-overflow-tooltip />
      <el-table-column prop="account" label="手机号" show-overflow-tooltip />
      <!-- <el-table-column prop="role_name" label="权限角色" show-overflow-tooltip /> -->
      <el-table-column prop="create_user_name" label="创建人" show-overflow-tooltip />
      <el-table-column prop="create_time" label="创建时间" show-overflow-tooltip />
      <el-table-column label="操作" width="150">
        <template slot-scope="scope">
          <a class="edit" @click="edit(scope.row)">编辑</a>
          <a v-if="scope.row.status == 2" class="edit" @click="setStatus(scope.row, 1)">启用</a>
          <a v-if="scope.row.status == 1" class="del" @click="setStatus(scope.row, 2)">禁用</a>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination v-if="tableData.length > 0" background layout="total, sizes, prev, pager, next, jumper" :current-page="pageControl.pageNumber" :page-size="pageControl.pageSize" :total="pageControl.total" @current-change="handleCurrentChange" @size-change="handleSizeChange" />
    <el-dialog :title="mode == 1 ? '新建员工' : '编辑员工'" :visible.sync="dialogFormVisible">
      <el-form ref="ruleForm" :model="groupOne" :rules="rules" class="demo-ruleForm" label-width="120px">
        <el-form-item label="员工姓名：" prop="nickname">
          <el-input v-model="groupOne.nickname" placeholder="请输入员工姓名" class="w100" clearable />
        </el-form-item>
        <el-form-item label="手机号：" prop="account">
          <el-input v-model="groupOne.account" placeholder="请输入员工手机号" class="w100" maxlength="11" clearable :disabled="mode == 2" />
        </el-form-item>
        <el-form-item v-if="mode == 1" label="默认登录密码：">
          <el-input placeholder="默认登录密码" class="w100" disabled value="111111" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  data() {
    const validateAccount = (rule, value, callback) => {
      if (value !== '' && value !== undefined) {
        if (!/^1[3456789]\d{9}$/.test(value)) {
          callback(new Error('请输入正确的手机号'))
        } else {
          callback()
        }
      } else {
        callback(new Error('请输入手机号'))
      }
    }
    return {
      mode: 1, // 1-新建 2-编辑
      dialogFormVisible: false,
      groupOne: {},
      rules: {
        nickname: [{ required: true, message: '请输入员工姓名', trigger: 'change' }],
        account: [{ required: true, validator: validateAccount, trigger: 'change' }],
        role_id: [{ required: true, message: '请选择权限角色', trigger: 'change' }]
      }
    }
  },
  computed: {
    ...mapGetters('accountInfo', ['tableData', 'queryControl', 'pageControl'])
  },
  created() {
    this.query(this.pageControl.pageNumber)
  },
  methods: {
    ...mapActions('accountInfo', ['operatorListFn', 'operatorStatusFn', 'editOperatorFn', 'addOperatorFn']),
    async query(pg) {
      this.pageControl.pageNumber = pg
      await this.operatorListFn({ keyword: this.queryControl.keyword, account: this.queryControl.account, page: pg, pageSize: this.pageControl.pageSize })
    },
    create() {
      this.mode = 1
      this.groupOne = {}
      this.dialogFormVisible = true
      this.$nextTick(() => {
        this.$refs.ruleForm.clearValidate(['nickname', 'account', 'role_id'])
      })
    },
    edit(item) {
      this.mode = 2
      this.groupOne = { ...item }
      this.dialogFormVisible = true
      this.$nextTick(() => {
        this.$refs.ruleForm.clearValidate(['nickname', 'account', 'role_id'])
      })
    },
    setStatus(item, type) {
      if (type === 2) {
        this.$confirm('禁用后该账号将无法登录（数据将保留），确认禁用此账号？', '禁用提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.operatorStatusFn({ account_id: item.id, status: type }).then(data => {
            if (data) {
              this.$message({ type: 'success', message: '禁用成功' })
              this.query(this.pageControl.pageNumber)
            }
          })
        }).catch(() => {})
      } else {
        this.operatorStatusFn({ account_id: item.id, status: type }).then(data => {
          if (data) {
            this.$message({ type: 'success', message: '启用成功' })
            this.query(this.pageControl.pageNumber)
          }
        })
      }
    },
    handleCurrentChange(val) {
      this.query(val)
    },
    handleSizeChange(val) {
      this.pageControl.pageSize = val
      this.query(1)
    },
    submitForm(formName) {
      this.$refs[formName].validate(async(valid) => {
        if (valid) {
          if (this.mode === 1) {
            const res = await this.addOperatorFn({
              account: this.groupOne.account,
              nickname: this.groupOne.nickname,
              role: this.groupOne.role_id
            })
            if (res.code === 200) {
              this.dialogFormVisible = false
              this.$message({ type: 'success', message: '新增成功' })
              this.query(this.pageControl.pageNumber)
            }
          } else {
            const res = await this.editOperatorFn({
              id: this.groupOne.id,
              account: this.groupOne.account,
              nickname: this.groupOne.nickname,
              role: this.groupOne.role_id
            })
            if (res.code === 200) {
              this.dialogFormVisible = false
              this.$message({ type: 'success', message: '编辑成功' })
              this.query(this.pageControl.pageNumber)
            }
          }
        }
      })
    },
    roleManage() {
      this.$router.push('/role')
    }
  }
}
</script>

<style lang="scss" scoped></style>
